import React, {useEffect, useState, isValidElement} from 'react';
import moment from 'moment';
import {BrowserRouter as Router, Route} from 'react-router-dom';

import s from './App.module.scss';
import Filter from './components/Filter';
import FilterModal from './components/FilterModal';
import DealsList from './components/DealsList';
import CitiesDels from './components/CitiesDeals';
import {convertMiliseconds} from './functions';

function App() {
  const [deals, setDeals] = useState([]);
  const [filteredDeals, setFilteredDeals] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);

  const [filters, setFilters] = useState({
    nights: 0,
    hotelRating: 0,
    preferedVacation: 'all',
    hotelStars: 0,
    from: moment(),
  });

  const clearFilters = () =>
    setFilters({
      nights: 0,
      hotelRating: 0,
      preferedVacation: 'all',
      hotelStars: 0,
      from: moment(),
    });

  useEffect(() => {
    fetch(
      'https://s3.eu-central-1.amazonaws.com/js.smartair.co.il/test/dealsJson.json'
    )
      .then(response => {
        return response.json();
      })
      .then(arr => {
        const objUniqueCites = arr.reduce(
          (obj, deal) => ({
            ...obj,
            [deal.cityName]: obj[deal.cityName]
              ? [...obj[deal.cityName], deal]
              : [deal],
          }),
          {}
        );

        setDeals(
          Object.values(objUniqueCites),
          setFilteredDeals(Object.values(objUniqueCites))
        );
      });
  }, []);

  useEffect(() => {
    setFilteredDeals(
      deals
        // filter by hotel stars
        .map(dealArr =>
          dealArr.filter(deal => {
            const {hotelData} = deal;
            if (filters.hotelStars === 0) return deal;
            return hotelData.Stars === filters.hotelStars;
          })
        )
        // filter by hotel rating
        .map(dealArr =>
          dealArr.filter(deal => {
            const {hotelData} = deal;
            if (filters.hotelRating  === 0) return deal;
            return hotelData.reviewScore >= filters.hotelRating;
          })
        )
        // filter by nights
        .map(dealArr =>
          dealArr.filter(deal => {
            if (filters.nights  === 0) return deal;
            const {flightData} = deal;
            const {outboundArrivalDate, inboundDepartureDate} = flightData;

            const toDate = arr => new Date(+arr[2], +arr[1] - 1, +arr[0]);

            const depDate = toDate(inboundDepartureDate.split('/'));
            const arrDate = toDate(outboundArrivalDate.split('/'));
            const nights = convertMiliseconds(depDate - arrDate, 'd');
            console.log(nights === filters.nights, nights, filters.nights);

            return nights === filters.nights;
          })
        )
        // filter by weekends
        .map(dealArr =>
          dealArr.filter(deal => {
            if (filters.preferedVacation === 'all') return deal;
            const {flightData} = deal;
            const {outboundArrivalDate, inboundDepartureDate} = flightData;

            const toDate = arr => new Date(+arr[2], +arr[1] - 1, +arr[0]);

            const depDate = toDate(inboundDepartureDate.split('/'));
            const arrDate = toDate(outboundArrivalDate.split('/'));
            const nights = convertMiliseconds(depDate - arrDate, 'd');

            const weekdays = ['Thursday', 'Friday', 'Saturday', 'Sunday'];
            let isWeekend = false;

            for (let index = 0; index < nights; index++) {
              if (
                weekdays.includes(
                  moment(depDate)
                    .add(index, 'day')
                    .format('dddd')
                )
              )
                isWeekend = true;
            }

            return isWeekend;
          })
        )
        // filter by date
        .map(dealArr =>
          dealArr.filter(deal => {
            const {flightData} = deal;
            const {outboundDepartureDate} = flightData;

            const toDate = arr => new Date(+arr[2], +arr[1] - 1, +arr[0]);

            const depDate = toDate(outboundDepartureDate.split('/'));

            //.subtract(1, 'days');
            return moment(depDate).isAfter(moment(filters.from).subtract(1, 'days'));
          })
        )
        .filter(deals => deals.length > 0)
    );
  }, [filters, deals]);

  return (
    <div>
      <Filter filters={filters} setFilters={setFilters} />
      {modalOpen ? (
        <FilterModal
          filters={filters}
          setFilters={setFilters}
          open={modalOpen}
          setOpen={setModalOpen}
          clearFilters={clearFilters}
        />
      ) : null}
      <button className={s.filterButton} onClick={() => setModalOpen(true)}>
        <span></span>
        <span></span>
        <span></span>
      </button>
      <Router>
        <Route path="/" exact>
          <h3 className={s.title}>Result from {filters.from.format('MMMM')}</h3>
          <DealsList deals={filteredDeals} />
        </Route>
        <Route path="/more-offers/:city">
          <CitiesDels deals={filteredDeals} />
        </Route>
      </Router>
    </div>
  );
}

export default App;
