import React, {useState} from 'react';
import {Link} from 'react-router-dom';

import s from './DealItem.module.css';
import DealContent from './DealContent';

const DealItem = ({deals}) => {
  const {cityPhoto, cityName} = deals[0];

  return (
    <div className={s.wrapper}>
      <img src={cityPhoto} alt="city photo" className={s.image} />

      <div className={s.blurWrapper}>
        <div className={s.blur}>
          <img src={cityPhoto} alt="city photo" className={s.blurImage} />
        </div>
      </div>
      <p className={s.caption}>
        {cityName}
        <span className={s.location}>
          <i className="fa fa-map-marker"></i>
        </span>
      </p>
      {deals.map((deal, i) => (
        <DealContent key={i} deal={deal} />
      ))}
      {/* <div className={s.moreOffers}> */}
        <Link to={`/more-offers/${cityName}`} className={s.moreOffers}>
          <i
            className="fa fa-chevron-down"
            style={{fontSize: '10px', marginRight: '5px'}}
            aria-hidden="true"
          ></i>{' '}
          More offers
        </Link>
      {/* </div> */}
    </div>
  );
};

export default DealItem;
