import React from 'react';
// import Select from 'react-select';
import s from './Select.module.scss';

import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import {
  createMuiTheme,
  makeStyles,
  ThemeProvider,
} from '@material-ui/core/styles';
import grey from '@material-ui/core/colors/grey';
import orange from '@material-ui/core/colors/orange';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

const theme = createMuiTheme({
  palette: {
    primary: orange,
    secondary: orange,
  },
  status: {
    danger: 'red',
  },
});

const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    width: 225,
    textAlign: 'right',
    background: 'transparent'
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  select: {
    //padding: '20px 8px 8px 38px'

  }
}));

const CustomSelect = ({options, handleChange, value, icon, label, style}) => {
  const classes = useStyles(theme);

  return (
    <ThemeProvider theme={theme}>
      <div className={s.selectWrap}>
        <div className={s.label}>{label}</div>
        <FormControl
          variant="outlined"
          className={`${classes.formControl} ${s.iconWrap}`}
          style={style}

        >
          {icon}
          <Select
            value={value}
            onChange={handleChange}
            className={classes.select}
          >
            {options.map(opt => (
              <MenuItem value={opt.value}>{opt.label}</MenuItem>
            ))}
          </Select>
        </FormControl>
      </div>
    </ThemeProvider>

    // <div className={s.selectWrap}>
    //   {icon}
    //   <select className={s.select} onChange={e => console.log(e.target.value)}>
    //   {options.map(opt => (
    //     <option className={s.option} value={opt.value}>{opt.label}</option>
    //   ))}
    // </select>
    // </div>
    //   <Select style={{ padding: '24px 16px 8px 38px', width: '225px'}} value={value} onChange={handleChange} options={options} />
  );
};

export default CustomSelect;
