import React, {useState} from 'react';

import s from './DealItem.module.css';
import DealContent from './DealContent';

const CityItem = ({deals}) => {
  return deals.map((deal,i) => {
    const {hotelData} = deal;
    const {Name, hotelPhotoUrl} = hotelData;

    return (
      <div key={i} className={s.wrapper} >
        <img src={hotelPhotoUrl} alt="hotel photo" className={s.image} />

        <div className={s.blurWrapper}>
          <div className={s.blur}>
            <img
              src={hotelPhotoUrl}
              alt="hotel photo"
              className={s.blurImage}
            />
          </div>
        </div>
        <p className={s.caption}>
          {Name}
          <span className={s.location}>
            <i className="fa fa-map-marker"></i>
          </span>
        </p>
        <DealContent deal={deal} />
      </div>
    );
  });
};

export default CityItem;
