import React, {useState} from 'react';

import s from './DealItem.module.css';
import {convertMiliseconds} from '../functions'


const DealContent = ({deal}) => {
  const [showDetails, setShowDetails] = useState(false);

  const {
    pricePerperson,
    hotelData,
    departureDate,
    ArrivalDate,
    flightData,
  } = deal;

  const {
    outboundDepartureHour,
    outboundDepartureDate,
    outboundArrivalDate,
    outboundArrivalHour,
    inboundDepartureHour,
    inboundDepartureDate,
    inboundArrivalHour,
    inboundArrivalDate,
    outboundTotalFlightTime,
    inboundTotalFlightTime
  } = flightData;
  const {Stars: rating, Name} = hotelData;

  const ratingContent = [];
  for (let i = 1; i <= 5; i++) {
    if (i <= rating)
      ratingContent.push(
        <div style={{color: '#F19A34', margin: '3px'}} key={i}>
          <i className="fa fa-star"></i>
        </div>
      );
    else
      ratingContent.push(
        <div style={{color: '#D7D9E2', margin: '3px'}} key={i}>
          <i className="fa fa-star"></i>
        </div>
      );
  }

  // calculate nights
  const toDate = arr => new Date(+arr[2], +arr[1] - 1, +arr[0]);

  const depDate = toDate(departureDate.split('/'));
  const arrDate = toDate(ArrivalDate.split('/'));
  const nights = convertMiliseconds(arrDate - depDate, 'd');

  const depDateStr = `${depDate.getDate()}. ${depDate.toLocaleString(
    'default',
    {month: 'long'}
  )} ${depDate.getFullYear()}`;
  const arDateStr = `${arrDate.getDate()}. ${arrDate.toLocaleString('default', {
    month: 'long',
  })} ${arrDate.getFullYear()}`;

  // calculate flight time

  // const oDepDate = toDate(outboundDepartureDate.split('/'));
  // oDepDate.setHours(outboundDepartureHour.split(':')[0]);
  // oDepDate.setMinutes(outboundDepartureHour.split(':')[1]);

  // const oArDate = toDate(outboundArrivalDate.split('/'));
  // oArDate.setHours(outboundArrivalHour.split(':')[0]);
  // oArDate.setMinutes(outboundArrivalHour.split(':')[1]);

  // oFlyingTime = convertMiliseconds(oArDate - oDepDate);

  // const iDepDate = toDate(inboundDepartureDate.split('/'));
  // iDepDate.setHours(inboundDepartureHour.split(':')[0]);
  // iDepDate.setMinutes(inboundDepartureHour.split(':')[1]);

  // const iArDate = toDate(inboundArrivalDate.split('/'));
  // iArDate.setHours(inboundArrivalHour.split(':')[0]);
  // iArDate.setMinutes(inboundArrivalHour.split(':')[1]);

  //const iFlyingTime = convertMiliseconds(iArDate - iDepDate);

  return (
    <>
      <div
        className={s.content}
        style={{backgroundColor: showDetails ? '#F7F7FA' : ''}}
        onClick={() => setShowDetails(!showDetails)}

      >
        <div className={s.contentWrap}>
          <div className={s.left}>
            <span>from</span>
            <span>${pricePerperson}</span>
            <span>per person</span>
            <div className={s.rating}>{ratingContent.reverse()}</div>
            <div
              className={s.detailsButton}
            >
              <i
                className="fa fa-chevron-down"
                style={{fontSize: '10px', marginRight: '5px'}}
                aria-hidden="true"
              ></i>
              Details
            </div>
          </div>
          <div className={s.right}>
            <span>
              {departureDate}-{ArrivalDate}
            </span>
            <span>{nights} nights</span>
            <span>{Name}</span>
          </div>
        </div>
        {showDetails ? (
          <>
            <div className={s.details}>
              <div className={s.detailsDate}>
                <span>{depDateStr}</span>
                <span>Flight</span>
              </div>
              <div className={s.detailsTime}>
                <div className={s.detailsLanding}>
                  <span>Landing time</span>
                  <span>
                    {outboundArrivalHour}{' '}
                    <i
                      className="fa fa-clock-o"
                      style={{fontSize: '16px'}}
                      aria-hidden="true"
                    ></i>
                  </span>
                </div>
                <div className={s.detailsHour}>
                  <span>
                    {/* {oFlyingTime.h}h {oFlyingTime.m}m */}
                    {outboundTotalFlightTime.toLowerCase()}
                  </span>
                  <div style={{display: 'flex', alignItems: 'center'}}>
                    <i
                      className="fa fa-map-marker"
                      style={{fontSize: '10px'}}
                      aria-hidden="true"
                    ></i>
                    <div className={s.line}></div>
                    <i
                      className="fa fa-sort-desc"
                      style={{fontSize: '12px', transform: 'rotate(90deg)'}}
                      aria-hidden="true"
                    ></i>
                  </div>
                  <span>0 stops</span>
                </div>
                <div className={s.detailsLanding}>
                  <span>Departure</span>
                  <span>
                    {outboundDepartureHour}{' '}
                    <i
                      className="fa fa-clock-o"
                      style={{fontSize: '16px'}}
                      aria-hidden="true"
                    ></i>
                  </span>
                </div>
                <i
                  className="fa fa-plane"
                  style={{
                    fontSize: '18px',
                    transform: 'rotate(-135deg)',
                    position: 'relative',
                    top: '-15px',
                    right: '-12px',
                    color: '#ccc',
                  }}
                  aria-hidden="true"
                ></i>
              </div>
              <div className={s.detailsDate}>
                <span>{arDateStr}</span>
                <span></span>
              </div>
              <div className={s.detailsTime}>
                <div className={s.detailsLanding}>
                  <span>Landing time</span>
                  <span>
                    {inboundArrivalHour}{' '}
                    <i
                      className="fa fa-clock-o"
                      style={{fontSize: '16px'}}
                      aria-hidden="true"
                    ></i>
                  </span>
                </div>
                <div className={s.detailsHour}>
                  <span>
                    {/* {iFlyingTime.h}h {iFlyingTime.m}m */}
                    {inboundTotalFlightTime.toLowerCase()}
                  </span>
                  <div style={{display: 'flex', alignItems: 'center'}}>
                    <i
                      className="fa fa-map-marker"
                      style={{fontSize: '10px'}}
                      aria-hidden="true"
                    ></i>
                    <div className={s.line}></div>
                    <i
                      className="fa fa-sort-desc"
                      style={{fontSize: '12px', transform: 'rotate(90deg)'}}
                      aria-hidden="true"
                    ></i>
                  </div>
                  <span>0 stops</span>
                </div>
                <div className={s.detailsLanding}>
                  <span>Return</span>
                  <span>
                    {inboundDepartureHour}{' '}
                    <i
                      className="fa fa-clock-o"
                      style={{fontSize: '16px'}}
                      aria-hidden="true"
                    ></i>
                  </span>
                </div>
                <i
                  className="fa fa-plane"
                  style={{
                    fontSize: '18px',
                    transform: 'rotate(-135deg)',
                    position: 'relative',
                    top: '-15px',
                    right: '-12px',
                    color: '#ccc',
                  }}
                  aria-hidden="true"
                ></i>
              </div>
            </div>
            <div className={s.accomodation}>
              <p>Accomodation</p>
              <div className={s.accomodationContent}>
                <div className={s.accomodationRating}>
                  <div className={s.rating}>{ratingContent}</div>
                  <div className={s.hotelName}>{hotelData.Name}</div>
                  <div className={s.reviewScore}>
                    <span>Excelent</span>{' '}
                    <span>({hotelData.reviewCount} reviews)</span>{' '}
                    <span>{hotelData.reviewScore}</span>
                  </div>
                </div>
                <img
                  className={s.accomodationImage}
                  src={hotelData.hotelPhotoUrl}
                />
              </div>
            </div>
            <button className={s.makeReservation}>Make reservation</button>
          </>
        ) : null}
      </div>
    </>
  );
};

export default DealContent