import React, {useState} from 'react';
import {Tab, Tabs, TabList, TabPanel} from 'react-tabs';
import moment from 'moment';
import DatePicker from './DatePicker';
import Select from './Select';
import s from './FilterModal.module.scss';

const FilterModal = ({filters, setFilters, setOpen, clearFilters}) => {
  const [form, setForm] = useState(filters);

  const handleChange = name => selectedOption => {
    setForm(form => ({...form, [name]: selectedOption.target.value}));
  };

  const handleSubmit = e => {
    e.preventDefault();
    setFilters(form);
  };

  const handleClearFilters = () => {
    clearFilters();
    setForm({
      nights: 0,
      hotelRating: 0,
      preferedVacation: 'all',
      hotelStars: 0,
      from: moment(),
    });
  };

  const clearFilterComponent = (
    <div className={s.clearFilter} onClick={handleClearFilters}>
      Clear all
    </div>
  );

  const selectModalSpecial = {width: '100%', marginLeft: '-2px'};

  return (
    <div className={s.formWrap}>
      <div className={s.modalHeader}>
        <p>Filter</p>
        <div onClick={() => setOpen(false)}>
          <i
            style={{color: '#D8D8D8', fontSize: '24px'}}
            className="fa fa-times"
            aria-hidden="true"
          ></i>
        </div>
      </div>
      <form className={s.form} onSubmit={handleSubmit}>
        <Tabs
          className={s.tabs}
          selectedTabClassName={s.tabActive}
          selectedTabPanelClassName={s.tabPanel}
        >
          <TabList className={s.tabList}>
            <Tab className={s.tab}>
              <i className="fa fa-calendar-check-o" aria-hidden="true"></i>
              <span>From</span>
            </Tab>
            <Tab className={s.tab}>
              <i className="fa fa-star" aria-hidden="true"></i>
              <span>
                Hotel <br /> Stars
              </span>
            </Tab>
            <Tab className={s.tab}>
              <i className="fa fa-calendar-o" aria-hidden="true"></i>
              <span>Prefered Vacation</span>
            </Tab>
            <Tab className={s.tab}>
              <i className="fa fa-star-o" aria-hidden="true"></i>
              <span>
                Hotel <br /> Ratings
              </span>
            </Tab>
            <Tab className={s.tab}>
              <i className="fa fa-bed" aria-hidden="true"></i>
              <span>How many nights?</span>
            </Tab>
          </TabList>

          <TabPanel>
            <div className={s.tabPanelText}>
              <div className={s.label}>From</div>
              {clearFilterComponent}
            </div>
            <DatePicker
              date={form.from}
              onChange={handleChange('from')}
              //   label="From"
              style={{width: '100%'}}
              icon={
                <i className="fa fa-calendar-check-o" aria-hidden="true"></i>
              }
            />
          </TabPanel>
          <TabPanel>
            <div className={s.tabPanelText}>
              <div className={s.label}>Hotel Stars</div>
              {clearFilterComponent}{' '}
            </div>
            <Select
              handleChange={handleChange('hotelStars')}
              options={[
                {value: 0, label: 'All'},
                {value: 3, label: '3'},
                {value: 4, label: '4'},
                {value: 5, label: '5'},
              ]}
              style={selectModalSpecial}
              //label="Hotel Stars"
              icon={<i className="fa fa-star" aria-hidden="true"></i>}
              value={form.hotelStars}
            />
          </TabPanel>
          <TabPanel>
            <div className={s.tabPanelText}>
              <div className={s.label}>Prefered Vacation</div>
              {clearFilterComponent}{' '}
            </div>
            <Select
              handleChange={handleChange('preferedVacation')}
              options={[
                {value: 'all', label: 'All days'},
                {value: 'weekend', label: 'Weekend'},
              ]}
              style={selectModalSpecial}
              //label="Prefered Vacation"
              icon={<i className="fa fa-calendar-o" aria-hidden="true"></i>}
              value={form.preferedVacation}
            />
          </TabPanel>
          <TabPanel>
            <div className={s.tabPanelText}>
              <div className={s.label}>Hotel Ratings</div>
              {clearFilterComponent}{' '}
            </div>
            <Select
              handleChange={handleChange('hotelRating')}
              options={[
                {value: 0, label: 'All'},
                {value: 3, label: '3+'},
                {value: 4, label: '4+'},
                {value: 5, label: '5+'},
              ]}
              style={selectModalSpecial}
              //label="Hotel Ratings"
              icon={<i className="fa fa-star-o" aria-hidden="true"></i>}
              value={form.hotelRating}
            />
          </TabPanel>
          <TabPanel>
            <div className={s.tabPanelText}>
              <div className={s.label}>How many nights?</div>
              {clearFilterComponent}{' '}
            </div>
            <Select
              handleChange={handleChange('nights')}
              options={[
                {value: 0, label: 'All'},
                {value: 3, label: '3+'},
                {value: 4, label: '4+'},
                {value: 5, label: '5+'},
              ]}
              style={selectModalSpecial}
              label="How many nights?"
              icon={<i className="fa fa-bed" aria-hidden="true"></i>}
              value={form.nights}
            />
          </TabPanel>
        </Tabs>
        <button className={s.button} type="submit">
          Filter
        </button>
      </form>
    </div>
  );
};

export default FilterModal;
