import React from 'react';

import DealItem from './DealItem';
import CityItem from './CityItem';
import s from './DealsList.module.css';

const DealsList = ({deals, cityDeal}) => {
  return (
    <div className={s.list}>
      {deals.length === 0 ? (
        <h5 className={s.information}>No flights by your filters.</h5>
      ) : (
        deals.map((deals, i) =>
          cityDeal ? (
            <CityItem key={i} deals={deals} />
          ) : (
            <DealItem key={i} deals={deals} />
          )
        )
      )}
    </div>
  );
};

export default DealsList;
