import React, {useState} from 'react';
import {SingleDatePicker} from 'react-dates';
import 'react-dates/initialize';
import styled from 'styled-components';
import 'react-dates/lib/css/_datepicker.css';

const StyledDatePickerWrapper = styled.div`
  position: relative;
  width: 225px;
  & .SingleDatePicker,
  .SingleDatePickerInput {
    border-color: #bbb;
    margin-top: 2px;
    .DateInput {
      width: 100%;
      // height: 40px;
      display: flex;

      .DateInput_input {
        font-size: 1rem;
        border-bottom: 0;
        padding: 16px 16px 16px 38px;
        background: transparent;
        text-align: right;
      }
    }

    .SingleDatePickerInput__withBorder {
      border-radius: 4px;
      overflow: hidden;

      :hover,
      .DateInput_input__focused {
        border: 1px solid orange;
      }

      .CalendarDay__selected {
        background: blue;
        border: blueviolet;
      }
    }

    .SingleDatePicker_picker.SingleDatePicker_picker {
      top: 43px;
      left: 2px;
      /* top: 43px !important;
      left: 2px !important; */
    }
  }
`;

export default function DatePicker({date, onChange, icon, label, style}) {
  const [focused, setFocused] = useState(false);

  const labelStyle = {
    position: 'absolute',
    right: '16px',
    top: '10px',
    zIndex: 1,
    textAlign: 'right',
    fontSize: '12px',
    color: '#999',
  };

  const iconStyle = {
    position: 'absolute',
    left: '16px',
    top: '25px',
    zIndex: 1,
    textAlign: 'right',
    fontSize: '16px',
    color: '#777',
  };

  return (
    <StyledDatePickerWrapper style={style} >
      <div style={labelStyle}>{label}</div>
      <div style={iconStyle}>{icon}</div>
      <SingleDatePicker
        numberOfMonths={1}
        onDateChange={date => onChange({target: {value: date}})}
        onFocusChange={({focused}) => setFocused(focused)}
        focused={focused}
        date={date}
        displayFormat="YYYY-MM-DD"
        //isDayBlocked={m => m.day() === 6 || m.day() === 0}
        hideKeyboardShortcutsPanel
        // withPortal
        withFullScreenPortal={window.innerWidth < 400}
      />
    </StyledDatePickerWrapper>
  );
}
