import React, {useState} from 'react';
import DatePicker from './DatePicker';
import Select from './Select';
import s from './Filter.module.scss';

const Filter = ({filters, setFilters}) => {
  const [form, setForm] = useState(filters);

  const handleChange = name => selectedOption => {

    setForm(form => ({...form, [name]: selectedOption.target.value}));
  };

  

  const handleSubmit = e =>  {
    e.preventDefault()
    setFilters(form)
  }

  return (
    <div className={s.formWrap}>
      <form className={s.form} onSubmit={handleSubmit}>
        <button className={s.button} type="submit">
          Filter
        </button>
        <Select
          handleChange={handleChange('nights')}
          options={[
            {value: 0, label: 'All'},
            {value: 3, label: '3+'},
            {value: 4, label: '4+'},
            {value: 5, label: '5+'},
          ]}
          label="How many nights?"
          icon={<i className="fa fa-bed" aria-hidden="true"></i>}
          value={form.nights}
        />
        <Select
          handleChange={handleChange('hotelRating')}
          options={[
            {value: 0, label: 'All'},
            {value: 3, label: '3+'},
            {value: 4, label: '4+'},
            {value: 5, label: '5+'},
          ]}
          label="Hotel Ratings"
          icon={<i className="fa fa-star-o" aria-hidden="true"></i>}
          value={form.hotelRating}
        />
        <Select
          handleChange={handleChange('preferedVacation')}
          options={[
            {value: 'all', label: 'All days'},
            {value: 'weekend', label: 'Weekend'},
          ]}
          label="Prefered Vacation"
          icon={<i className="fa fa-calendar-o" aria-hidden="true"></i>}
          value={form.preferedVacation}
        />
        <Select
          handleChange={handleChange('hotelStars')}
          options={[
            {value: 0, label: 'All'},
            {value: 3, label: '3'},
            {value: 4, label: '4'},
            {value: 5, label: '5'},
          ]}
          label="Hotel Stars"
          icon={<i className="fa fa-star" aria-hidden="true"></i>}
          value={form.hotelStars}
        />
        <DatePicker
          date={form.from}
          onChange={handleChange('from')}
          label="From"
          icon={<i className="fa fa-calendar-check-o" aria-hidden="true"></i>}
        />
      </form>
    </div>
  );
};

export default Filter;
